import React, { useEffect, useState } from "react";
import { message } from "antd";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import {
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
} from "@material-ui/core";
// @mui/material components
import {
  Button,
  Box,
  CircularProgress,
  TextField,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Stack,
  IconButton,
  DialogContentText,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";

// core components
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";

// other components
import styles2 from "assets/jss/material-dashboard-react/components/tableStyle.js";
import RegularButton from "components/CustomButtons/Button";
import {
  getAllSpeakersAPI,
  addSpeakerAPI,
  getSpeakerByIdAPI,
  updateSpeakerAPI,
  deleteSpeakerAPI,
} from "services";
import { Editor } from "react-draft-wysiwyg";
import draftToHtml from "draftjs-to-html";
import {
  convertToRaw,
  convertFromHTML,
  ContentState,
  EditorState,
} from "draft-js";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "bold",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);
const useStyles2 = makeStyles(styles2);

const Speakers = () => {
  const classes = useStyles();
  const classes2 = useStyles2();

  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);
  const [open3, setOpen3] = useState(false);
  const [updateM, setUpdateM] = useState(false);
  const [loading, setLoading] = useState(true);

  const [id, setId] = useState();
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [image, setImage] = useState("");
  const [position, setPosition] = useState("");
  const [fbLink, setFbLink] = useState("");
  const [twitterLink, setTwitterLink] = useState("");
  const [instagramLink, setInstagramLink] = useState("");
  const [linkedinLink, setLinkedinLink] = useState("");
  const [editorState, setEditorState] = useState("");
  const [speakersData, setSpeakersData] = useState([]);

  const [formErrors, setFormErrors] = useState([]);

  // ----------------------- Validation -------------------------
  const handleValidation = () => {
    let formErrors = {};
    let formIsValid = true;

    if (!name) {
      formIsValid = false;
      formErrors["name_error"] = "Name is required.";
    }
    if (!position) {
      formIsValid = false;
      formErrors["position_error"] = "Position is required.";
    }
    if (!description) {
      formIsValid = false;
      formErrors["description_error"] = "Description is required.";
    }
    if (!image) {
      formIsValid = false;
      formErrors["image_error"] = "Image is required.";
    }

    setFormErrors(formErrors);
    return formIsValid;
  };

  // ------- Reset Fields Function --------
  const resetFields = () => {
    setName("");
    setImage("");
    setPosition("");
    setFbLink("");
    setTwitterLink("");
    setInstagramLink("");
    setLinkedinLink("");
    setDescription("");
    setEditorState("");
    setFormErrors([]);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    resetFields();
  };

  const handleOpen2 = () => {
    setOpen2(true);
  };

  const handleClose2 = () => {
    setOpen2(false);
    resetFields();
  };

  const handleClose3 = () => {
    setOpen3(false);
    resetFields();
  };

  const getAllSpeakersAPIF = async () => {
    getAllSpeakersAPI()
      .then((res) => {
        try {
          if (res.data.data) {
            setSpeakersData(res.data.data);
            setLoading(false);
          }
        } catch (error) {
          console.log("getAllSpeakersAPI catch error ", JSON.stringify(error));
        }
      })
      .catch((err) => {
        console.log("getAllSpeakers err ", JSON.stringify(err));
      });
  };

  const getSpeakerByIdAPIF = async (id) => {
    getSpeakerByIdAPI(id)
      .then((res) => {
        try {
          if (res.data.data) {
            setId(res.data.data[0].id);
            setImage(res.data.data[0].image);
            setName(res.data.data[0].name);
            setPosition(res.data.data[0].position);
            setFbLink(res.data.data[0].fbLink);
            setTwitterLink(res.data.data[0].twitterLink);
            setInstagramLink(res.data.data[0].instagramLink);
            setLinkedinLink(res.data.data[0].linkedinLink);
            setDescription(res.data.data[0].description);

            let DesData = ContentState.createFromBlockArray(
              convertFromHTML(res.data.data[0].description)
            );
            let MainDesData = EditorState.createWithContent(DesData);
            setEditorState(MainDesData);
          }
        } catch (error) {
          console.log("getSpeakerByIdAPI catch error ", JSON.stringify(error));
        }
      })
      .catch((err) => {
        console.log("getSpeakerByIdAPI err ", JSON.stringify(err));
      });
  };

  const addSpeakerAPIF = async () => {
    let data = new FormData();
    data.append("name", name);
    data.append("position", position);
    data.append("fbLink", fbLink);
    data.append("twitterLink", twitterLink);
    data.append("instagramLink", instagramLink);
    data.append("linkedinLink", linkedinLink);
    data.append("description", description);
    data.append("image", image);

    addSpeakerAPI(data)
      .then((res) => {
        console.log("addSpeaker res ", JSON.stringify(res));
        handleClose();
        message.success("Speaker added successfully");
        getAllSpeakersAPIF();
      })
      .catch((err) => {
        console.log("addSpeaker err ", JSON.stringify(err));
      });
  };

  const updateSpeakerAPIF = async () => {
    let data = new FormData();
    data.append("id", id);
    data.append("name", name);
    data.append("position", position);
    data.append("fbLink", fbLink);
    data.append("twitterLink", twitterLink);
    data.append("instagramLink", instagramLink);
    data.append("linkedinLink", linkedinLink);
    data.append("description", description);
    data.append("image", image);

    updateSpeakerAPI(data)
      .then((res) => {
        console.log("updateSpeaker res ", JSON.stringify(res));
        handleClose2();
        message.success("Speaker updated successfully");
        getAllSpeakersAPIF();
      })
      .catch((err) => {
        console.log("updateSpeaker err ", JSON.stringify(err));
      });
  };

  const deleteSpeakerAPIF = async () => {
    await deleteSpeakerAPI(id)
      .then((res) => {
        console.log("deleteSpeaker res ", JSON.stringify(res));
        handleClose3();
        message.success("Speaker deleted successfully");
        getAllSpeakersAPIF();
      })
      .catch((err) => {
        console.log("deleteSpeaker err ", JSON.stringify(err));
      });
  };

  useEffect(() => {
    getAllSpeakersAPIF();
  }, []);

  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <RegularButton
          color="success"
          size="sm"
          style={{ float: "right", marginBottom: 40 }}
          onClick={handleOpen}
        >
          Add Speaker
        </RegularButton>
        <Dialog fullWidth open={open} onClose={handleClose}>
          <DialogTitle marginBottom={2} textAlign="center">
            Add Speaker
          </DialogTitle>
          <DialogContent>
            <Stack spacing={1}>
              <Button fullWidth variant="contained" component="label">
                Upload Image*
                <input
                  type="file"
                  accept="image/*"
                  hidden
                  onChange={(e) => {
                    setImage(e.target.files[0]);
                  }}
                />
              </Button>
              {image ? <p>Image Uploaded Successfully.</p> : null}
              {formErrors.image_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.image_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="name"
                label="Name*"
                type="text"
                fullWidth
                variant="standard"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              {formErrors.name_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.name_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="position"
                label="Position*"
                type="text"
                fullWidth
                variant="standard"
                value={position}
                onChange={(e) => {
                  setPosition(e.target.value);
                }}
              />
              {formErrors.position_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.position_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="fbLink"
                label="Facebook Link"
                type="text"
                fullWidth
                variant="standard"
                value={fbLink}
                onChange={(e) => {
                  setFbLink(e.target.value);
                }}
              />
              <TextField
                margin="dense"
                id="twitterLink"
                label="Twitter Link"
                type="text"
                fullWidth
                variant="standard"
                value={twitterLink}
                onChange={(e) => {
                  setTwitterLink(e.target.value);
                }}
              />
              <TextField
                margin="dense"
                id="instagramLink"
                label="Instagram Link"
                type="text"
                fullWidth
                variant="standard"
                value={instagramLink}
                onChange={(e) => {
                  setInstagramLink(e.target.value);
                }}
              />
              <TextField
                margin="dense"
                id="linkedinLink"
                label="Linkedin Link"
                type="text"
                fullWidth
                variant="standard"
                value={linkedinLink}
                onChange={(e) => {
                  setLinkedinLink(e.target.value);
                }}
              />
              <Editor
                align="left"
                editorState={editorState}
                toolbarClassName="toolbarClassName"
                wrapperClassName="wrapperClassName"
                editorClassName="editorClassName"
                placeholder="Enter Description*"
                style={{ backgrondColor: "white" }}
                editorStyle={{ color: "#164242" }}
                onEditorStateChange={(e) => {
                  setEditorState(e);
                }}
                onChange={() => {
                  setDescription(
                    draftToHtml(convertToRaw(editorState.getCurrentContent()))
                  );
                }}
              />
              {formErrors.description_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.description_error}
                  </p>
                </div>
              )}
            </Stack>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleClose()}>Cancel</Button>
            <Button
              onClick={() => {
                if (handleValidation()) {
                  addSpeakerAPIF();
                } else {
                  message.info("Please complete the validation");
                }
              }}
            >
              Proceed
            </Button>
          </DialogActions>
        </Dialog>
        <Dialog fullWidth open={open2} onClose={handleClose2}>
          <DialogTitle marginBottom={2} textAlign="center">
            Update Speaker
          </DialogTitle>
          <DialogContent>
            <Stack spacing={1}>
              <Button fullWidth variant="contained" component="label">
                Upload Logo*
                <input
                  type="file"
                  accept="image/*"
                  hidden
                  onChange={(e) => {
                    setImage(e.target.files[0]);
                    setUpdateM(true);
                  }}
                />
              </Button>
              {updateM && image ? (
                <p>Image Uploaded Successfully.</p>
              ) : image ? (
                <p>Image Already Available.</p>
              ) : null}
              {formErrors.image_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.image_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="name"
                label="Name*"
                type="text"
                fullWidth
                variant="standard"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              {formErrors.name_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.name_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="position"
                label="Position*"
                type="text"
                fullWidth
                variant="standard"
                value={position}
                onChange={(e) => {
                  setPosition(e.target.value);
                }}
              />
              {formErrors.position_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.position_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="fbLink"
                label="Facebook Link"
                type="text"
                fullWidth
                variant="standard"
                value={fbLink}
                onChange={(e) => {
                  setFbLink(e.target.value);
                }}
              />
              <TextField
                margin="dense"
                id="twitterLink"
                label="Twitter Link"
                type="text"
                fullWidth
                variant="standard"
                value={twitterLink}
                onChange={(e) => {
                  setTwitterLink(e.target.value);
                }}
              />
              <TextField
                margin="dense"
                id="instagramLink"
                label="Instagram Link"
                type="text"
                fullWidth
                variant="standard"
                value={instagramLink}
                onChange={(e) => {
                  setInstagramLink(e.target.value);
                }}
              />
              <TextField
                margin="dense"
                id="linkedinLink"
                label="Linkedin Link"
                type="text"
                fullWidth
                variant="standard"
                value={linkedinLink}
                onChange={(e) => {
                  setLinkedinLink(e.target.value);
                }}
              />
              <Editor
                align="left"
                editorState={editorState}
                toolbarClassName="toolbarClassName"
                wrapperClassName="wrapperClassName"
                editorClassName="editorClassName"
                placeholder="Enter Description*"
                style={{ backgrondColor: "white" }}
                editorStyle={{ color: "#164242" }}
                onEditorStateChange={(e) => {
                  setEditorState(e);
                }}
                onChange={() => {
                  setDescription(
                    draftToHtml(convertToRaw(editorState.getCurrentContent()))
                  );
                }}
              />
              {formErrors.description_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.description_error}
                  </p>
                </div>
              )}
            </Stack>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleClose2()}>Cancel</Button>
            <Button
              onClick={() => {
                if (handleValidation()) {
                  updateSpeakerAPIF();
                } else {
                  message.info("Please complete the validation");
                }
              }}
            >
              Proceed
            </Button>
          </DialogActions>
        </Dialog>
        <Dialog
          open={open3}
          onClose={handleClose3}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">Delete Speaker</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              Are You Sure, You want to delete this speaker?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose3}>No</Button>
            <Button onClick={() => deleteSpeakerAPIF()}>Yes</Button>
          </DialogActions>
        </Dialog>
        <Card>
          <CardHeader color="success">
            <h4 className={classes.cardTitleWhite}>Speakers</h4>
          </CardHeader>
          <CardBody>
            <div className={classes2.tableResponsive}>
              {loading ? (
                <Box sx={{ display: "flex" }}>
                  <CircularProgress color="success" />
                </Box>
              ) : (
                <Table style={{display: "block",
                  overflowX: "auto",
                  whiteSpace: "nowrap"}} className={classes2.table}>
                  <TableHead className={classes2["success" + "TableHeader"]}>
                    <TableRow className={classes2.tableHeadRow}>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        S.No.
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Image
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Name
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Position
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Facebook
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Twitter
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Instagram
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Linkedin
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Description
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Actions
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {speakersData?.map((data, index) => {
                      let a = data.image;
                      let b = "/";
                      let position = 6;
                      let images = [
                        a?.slice(0, position),
                        b,
                        a?.slice(position),
                      ]?.join("");

                      return (
                        <TableRow
                          key={data.id}
                          className={classes2.tableBodyRow}
                        >
                          <TableCell className={classes2.tableCell}>
                            {index + 1}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            <img
                              // src={`http://localhost:8000/${images}`}
                              src={`/${images}`}
                              style={{ height: 40, width: 40 }}
                            ></img>
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.name}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.position}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.fbLink}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.twitterLink}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.instagramLink}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.linkedinLink}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.description}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {
                              <Stack direction="row">
                                <IconButton
                                  onClick={() => {
                                    getSpeakerByIdAPIF(data.id);
                                    setTimeout(() => {
                                      handleOpen2();
                                    }, 500);
                                  }}
                                  color="primary"
                                  aria-label="edit"
                                >
                                  <Edit />
                                </IconButton>
                                <IconButton
                                  onClick={() => {
                                    getSpeakerByIdAPIF(data.id);
                                    setTimeout(() => {
                                      setOpen3(true);
                                    }, 200);
                                  }}
                                  color="error"
                                  aria-label="delete"
                                >
                                  <Delete />
                                </IconButton>
                              </Stack>
                            }
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  </TableBody>
                </Table>
              )}
            </div>
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
};

export default Speakers;
