import React, { useState, useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import {
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
} from "@material-ui/core";
import { Box, CircularProgress } from "@mui/material";
// core components
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import { getAllContactUsAPI } from "services";

// other components
import styles2 from "assets/jss/material-dashboard-react/components/tableStyle.js";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "bold",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);
const useStyles2 = makeStyles(styles2);

const ContactUs = () => {
  const classes = useStyles();
  const classes2 = useStyles2();

  const [allContactUs, setAllContactUs] = useState([]);
  const [loading, setLoading] = useState(true);

  const getAllContactUsAPIF = async () => {
    getAllContactUsAPI()
      .then((res) => {
        try {
          if (res.data.data) {
            setAllContactUs(res.data.data);
            setLoading(false);
          }
        } catch (error) {
          console.log("getAllContactUsAPI catch error ", JSON.stringify(error));
        }
      })
      .catch((err) => {
        console.log("getAllContactUsAPI err ", JSON.stringify(err));
      });
  };

  useEffect(() => {
    getAllContactUsAPIF();
  }, []);
  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="rose">
            <h4 className={classes.cardTitleWhite}>Contact Us</h4>
          </CardHeader>
          <CardBody>
            <div className={classes2.tableResponsive}>
              {loading ? (
                <Box sx={{ display: "flex" }}>
                  <CircularProgress color="error" />
                </Box>
              ) : (
                <Table className={classes2.table}>
                  <TableHead className={classes2["rose" + "TableHeader"]}>
                    <TableRow className={classes2.tableHeadRow}>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        S.No.
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Name
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Email
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Phone
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Message
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {allContactUs?.map((data, index) => {
                      return (
                        <TableRow
                          className={classes2.tableBodyRow}
                          key={data.id}
                        >
                          <TableCell className={classes2.tableCell}>
                            {index + 1}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.name}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.email}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.phone}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.message}
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  </TableBody>
                </Table>
              )}
            </div>
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
};

export default ContactUs;
