import axios from "axios";

// const baseURL = "http://localhost:8000/api";
const baseURL = "/api";

// ----------------- Brands APIs Start -----------------

export const getAllBrandsAPI = async () => {
  let res = await axios.get(`${baseURL}/getAllBrands`);
  return res;
};

export const getBrandByIdAPI = async (id) => {
  let res = await axios.get(`${baseURL}/getBrandById?id=${id}`);
  return res;
};

export const addBrandAPI = async (data) => {
  let res = await axios.post(`${baseURL}/addBrand`, data);
  return res;
};

export const updateBrandAPI = async (data) => {
  let res = await axios.put(`${baseURL}/updateBrand`, data);
  return res;
};

export const deleteBrandAPI = async (id) => {
  let res = await axios.delete(`${baseURL}/deleteBrand?id=${id}`);
  return res;
};

// ----------------- Brands APIs End -----------------

