import {
  Card,
  TextField,
  Button,
} from "@mui/material";
import React, { useState } from "react";
import Swal from "sweetalert2";

function Login() {
  const paperStyle = {
    padding: 20,
    height: "70vh",
    width: 280,
    margin: "20px auto",
  };

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const login = () => {
    if (email === "admin@admin.com" && password === "admin123") {
      localStorage.setItem("isLogin", true);
      window.location.reload();
    } else {
      Swal.fire({
        title: "Wrong Login Details!",
        text: "You are trying to login with wrong username or password",
        icon: "warning",
      })
    }
  };
  return (
    <div
      style={{
        backgroundImage: `url(https://images.pexels.com/photos/844124/pexels-photo-844124.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2)`,
        width: "100vw",
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Card
        sx={{
          borderRadius: 5,
          padding: 3,
          height: 360,
          width: 420,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <div>
          <h3>Crytpo Hub</h3>
        </div>
        <TextField
          sx={{ marginTop: 2 }}
          label="Username"
          variant="outlined"
          fullWidth
          required
          value={email}
          onChange={(e) => {
            setEmail(e.target.value);
          }}
        />
        <TextField
          sx={{ marginTop: 2 }}
          label="Password"
          type="password"
          variant="outlined"
          fullWidth
          required
          value={password}
          onChange={(e) => {
            setPassword(e.target.value);
          }}
        />
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            marginTop: 15,
          }}
        >
          <Button
            variant="contained"
            align="center"
            size="large"
            sx={{ backgroundColor: "black" }}
            onClick={login}
          >
            Sign in
          </Button>
        </div>
      </Card>
    </div>
  );
}

export default Login;
