import React from "react";
import { makeStyles } from "@material-ui/core/styles";

// core components
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";

// other components
import styles2 from "assets/jss/material-dashboard-react/components/tableStyle.js";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "bold",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);
const useStyles2 = makeStyles(styles2);

const Dashboard = () => {
  const classes = useStyles();
  const classes2 = useStyles2();

  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card style={{height: 300}}>
          <CardHeader color="primary">
            <h4 style={{textAlign: "center"}} className={classes.cardTitleWhite}>Welcome to CrytpoHub Admin Dashboard</h4>
          </CardHeader>
          <CardBody>
            <div className={classes2.tableResponsive}>
              <h6 style={{textAlign: "center", color: "black"}} className={classes.cardTitleWhite}>
                You can now customize your website.
              </h6>
            </div>
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
};

export default Dashboard;
