import React, { useEffect, useState } from "react";
import { message } from "antd";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import {
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
} from "@material-ui/core";
// @mui/material components
import {
  Button,
  Box,
  TextField,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Stack,
  IconButton,
  DialogContentText,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";

// core components
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";

// other components
import styles2 from "assets/jss/material-dashboard-react/components/tableStyle.js";
import RegularButton from "components/CustomButtons/Button";
import {
  getAllBrandsAPI,
  addBrandAPI,
  getBrandByIdAPI,
  updateBrandAPI,
  deleteBrandAPI,
} from "services";
import { Editor } from "react-draft-wysiwyg";
import draftToHtml from "draftjs-to-html";
import {
  convertToRaw,
  convertFromHTML,
  ContentState,
  EditorState,
} from "draft-js";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "bold",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);
const useStyles2 = makeStyles(styles2);

const Brands = () => {
  const classes = useStyles();
  const classes2 = useStyles2();

  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);
  const [open3, setOpen3] = useState(false);
  const [updateM, setUpdateM] = useState(false);
  const [loading, setLoading] = useState(true);

  const [id, setId] = useState();
  const [name, setName] = useState("");
  const [category, setCategory] = useState("");
  const [link, setLink] = useState("");
  const [description, setDescription] = useState("");
  const [image, setImage] = useState("");
  const [editorState, setEditorState] = useState("");
  const [brandsData, setBrandsData] = useState([]);

  const [formErrors, setFormErrors] = useState([]);

  // ----------------------- Validation -------------------------
  const handleValidation = () => {
    let formErrors = {};
    let formIsValid = true;

    if (!name) {
      formIsValid = false;
      formErrors["name_error"] = "Name is required.";
    }
    if (!category) {
      formIsValid = false;
      formErrors["category_error"] = "Category is required.";
    }
    if (!link) {
      formIsValid = false;
      formErrors["link_error"] = "Link is required.";
    }
    if (!description) {
      formIsValid = false;
      formErrors["description_error"] = "Description is required.";
    }
    if (!image) {
      formIsValid = false;
      formErrors["image_error"] = "Image is required.";
    }

    setFormErrors(formErrors);
    return formIsValid;
  };

  // ------- Reset Fields Function --------
  const resetFields = () => {
    setName("");
    setCategory("");
    setImage("");
    setLink("");
    setDescription("");
    setEditorState("");
    setFormErrors([]);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    resetFields();
  };

  const handleOpen2 = () => {
    setOpen2(true);
  };

  const handleClose2 = () => {
    setOpen2(false);
    resetFields();
  };

  const handleClose3 = () => {
    setOpen3(false);
    resetFields();
  };

  const getAllBrandsAPIF = async () => {
    getAllBrandsAPI()
      .then((res) => {
        try {
          if (res.data.data) {
            setBrandsData(res.data.data);
            setLoading(false);
          }
        } catch (error) {
          console.log("getAllBrandsAPI catch error ", JSON.stringify(error));
        }
      })
      .catch((err) => {
        console.log("getAllBrands err ", JSON.stringify(err));
      });
  };

  const getBrandByIdAPIF = async (id) => {
    getBrandByIdAPI(id)
      .then((res) => {
        try {
          if (res.data.data) {
            setId(res.data.data[0].id);
            setImage(res.data.data[0].image);
            setName(res.data.data[0].name);
            setLink(res.data.data[0].link);
            setCategory(res.data.data[0].category);
            setDescription(res.data.data[0].description);

            let DesData = ContentState.createFromBlockArray(
              convertFromHTML(res.data.data[0].description)
            );
            let MainDesData = EditorState.createWithContent(DesData);
            setEditorState(MainDesData);
          }
        } catch (error) {
          console.log("getBrandByIdAPI catch error ", JSON.stringify(error));
        }
      })
      .catch((err) => {
        console.log("getBrandByIdAPI err ", JSON.stringify(err));
      });
  };

  const addBrandAPIF = async () => {
    let data = new FormData();
    data.append("name", name);
    data.append("category", category);
    data.append("link", link);
    data.append("description", description);
    data.append("image", image);

    addBrandAPI(data)
      .then((res) => {
        console.log("addBrand res ", JSON.stringify(res));
        handleClose();
        message.success("Sponsor added successfully");
        getAllBrandsAPIF();
      })
      .catch((err) => {
        console.log("addBrand err ", JSON.stringify(err));
      });
  };

  const updateBrandAPIF = async () => {
    let data = new FormData();
    data.append("id", id);
    data.append("name", name);
    data.append("category", category);
    data.append("link", link);
    data.append("description", description);
    data.append("image", image);

    updateBrandAPI(data)
      .then((res) => {
        console.log("updateBrand res ", JSON.stringify(res));
        handleClose2();
        message.success("Sponsor updated successfully");
        getAllBrandsAPIF();
      })
      .catch((err) => {
        console.log("updateBrand err ", JSON.stringify(err));
      });
  };

  const deleteBrandAPIF = async () => {
    await deleteBrandAPI(id)
      .then((res) => {
        console.log("deleteBrand res ", JSON.stringify(res));
        handleClose3();
        message.success("Sponsor deleted successfully");
        getAllBrandsAPIF();
      })
      .catch((err) => {
        console.log("deleteBrand err ", JSON.stringify(err));
      });
  };

  useEffect(() => {
    getAllBrandsAPIF();
  }, []);

  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <RegularButton
          color="primary"
          size="sm"
          style={{ float: "right", marginBottom: 40 }}
          onClick={handleOpen}
        >
          Add Sponsor
        </RegularButton>
        <Dialog fullWidth open={open} onClose={handleClose}>
          <DialogTitle marginBottom={2} textAlign="center">
            Add Sponsor
          </DialogTitle>
          <DialogContent>
            <Stack spacing={1}>
              <Button fullWidth variant="contained" component="label">
                Upload Logo*
                <input
                  type="file"
                  accept="image/*"
                  hidden
                  onChange={(e) => {
                    setImage(e.target.files[0]);
                  }}
                />
              </Button>
              {image ? <p>Image Uploaded Successfully.</p> : null}
              {formErrors.image_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.image_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="name"
                label="Name*"
                type="text"
                fullWidth
                variant="standard"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              {formErrors.name_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.name_error}
                  </p>
                </div>
              )}
              <FormControl variant="standard">
                <InputLabel id="demo-simple-select-standard-label">
                  Category
                </InputLabel>
                <Select
                  labelId="demo-simple-select-standard-label"
                  id="demo-simple-select-standard"
                  value={category}
                  onChange={(e) => {
                    setCategory(e.target.value);
                  }}
                  label="Category"
                  fullWidth
                >
                  <MenuItem value="Platinum">Platinum</MenuItem>
                  <MenuItem value="Gold">Gold</MenuItem>
                  <MenuItem value="Silver">Silver</MenuItem>
                </Select>
              </FormControl>
              {formErrors.category_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.category_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="link"
                label="Link*"
                type="url"
                fullWidth
                variant="standard"
                value={link}
                onChange={(e) => {
                  setLink(e.target.value);
                }}
              />
              {formErrors.link_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.link_error}
                  </p>
                </div>
              )}
              <Editor
                align="left"
                editorState={editorState}
                toolbarClassName="toolbarClassName"
                wrapperClassName="wrapperClassName"
                editorClassName="editorClassName"
                placeholder="Enter Description*"
                style={{ backgrondColor: "white" }}
                editorStyle={{ color: "#164242" }}
                onEditorStateChange={(e) => {
                  setEditorState(e);
                }}
                onChange={() => {
                  setDescription(
                    draftToHtml(convertToRaw(editorState.getCurrentContent()))
                  );
                }}
              />
              {formErrors.description_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.description_error}
                  </p>
                </div>
              )}
            </Stack>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleClose()}>Cancel</Button>
            <Button
              onClick={() => {
                if (handleValidation()) {
                  addBrandAPIF();
                }
              }}
            >
              Proceed
            </Button>
          </DialogActions>
        </Dialog>
        <Dialog fullWidth open={open2} onClose={handleClose2}>
          <DialogTitle marginBottom={2} textAlign="center">
            Update Sponsor
          </DialogTitle>
          <DialogContent>
            <Stack spacing={1}>
              <Button fullWidth variant="contained" component="label">
                Upload Logo*
                <input
                  type="file"
                  accept="image/*"
                  hidden
                  onChange={(e) => {
                    setImage(e.target.files[0]);
                    setUpdateM(true);
                  }}
                />
              </Button>
              {updateM && image ? (
                <p>Image Uploaded Successfully.</p>
              ) : image ? (
                <p>Image Already Available.</p>
              ) : null}
              {formErrors.image_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.image_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="name"
                label="Name*"
                type="text"
                fullWidth
                variant="standard"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              {formErrors.name_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.name_error}
                  </p>
                </div>
              )}
              <FormControl variant="standard">
                <InputLabel id="demo-simple-select-standard-label">
                  Category
                </InputLabel>
                <Select
                  labelId="demo-simple-select-standard-label"
                  id="demo-simple-select-standard"
                  value={category}
                  onChange={(e) => {
                    setCategory(e.target.value);
                  }}
                  label="Category"
                  fullWidth
                >
                  <MenuItem value="Platinum">Platinum</MenuItem>
                  <MenuItem value="Gold">Gold</MenuItem>
                  <MenuItem value="Silver">Silver</MenuItem>
                </Select>
              </FormControl>
              {formErrors.category_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.category_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="link"
                label="Link*"
                type="url"
                fullWidth
                variant="standard"
                value={link}
                onChange={(e) => {
                  setLink(e.target.value);
                }}
              />
              {formErrors.link_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.link_error}
                  </p>
                </div>
              )}
              <Editor
                align="left"
                editorState={editorState}
                toolbarClassName="toolbarClassName"
                wrapperClassName="wrapperClassName"
                editorClassName="editorClassName"
                placeholder="Enter Description*"
                style={{ backgrondColor: "white" }}
                editorStyle={{ color: "#164242" }}
                onEditorStateChange={(e) => {
                  setEditorState(e);
                }}
                onChange={() => {
                  setDescription(
                    draftToHtml(convertToRaw(editorState.getCurrentContent()))
                  );
                }}
              />
              {formErrors.description_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.description_error}
                  </p>
                </div>
              )}
            </Stack>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleClose2()}>Cancel</Button>
            <Button
              onClick={() => {
                if (handleValidation()) {
                  updateBrandAPIF();
                }
              }}
            >
              Proceed
            </Button>
          </DialogActions>
        </Dialog>
        <Dialog
          open={open3}
          onClose={handleClose3}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">Delete Sponsor</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              Are You Sure, You want to delete this Sponsor?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose3}>No</Button>
            <Button onClick={() => deleteBrandAPIF()}>Yes</Button>
          </DialogActions>
        </Dialog>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>Sponsors</h4>
          </CardHeader>
          <CardBody>
            <div className={classes2.tableResponsive}>
              {loading ? (
                <Box sx={{ display: "flex" }}>
                  <CircularProgress color="secondary" />
                </Box>
              ) : (
                <Table className={classes2.table}>
                  <TableHead className={classes2["primary" + "TableHeader"]}>
                    <TableRow className={classes2.tableHeadRow}>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        S.No.
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Image
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Name
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Category
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Link
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Description
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Actions
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {brandsData?.map((data, index) => {
                      let a = data.image;
                      let b = "/";
                      let position = 6;
                      let images = [
                        a?.slice(0, position),
                        b,
                        a?.slice(position),
                      ]?.join("");

                      return (
                        <TableRow
                          key={data.id}
                          className={classes2.tableBodyRow}
                        >
                          <TableCell className={classes2.tableCell}>
                            {index + 1}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            <img
                              // src={`http://localhost:8000/${images}`}
                              src={`/${images}`}
                              style={{ height: 40, width: 40 }}
                            ></img>
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.name}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.category}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.link}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.description}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {
                              <Stack direction="row">
                                <IconButton
                                  onClick={() => {
                                    getBrandByIdAPIF(data.id);
                                    setTimeout(() => {
                                      handleOpen2();
                                    }, 500);
                                  }}
                                  color="primary"
                                  aria-label="edit"
                                >
                                  <Edit />
                                </IconButton>
                                <IconButton
                                  onClick={() => {
                                    getBrandByIdAPIF(data.id);
                                    setTimeout(() => {
                                      setOpen3(true);
                                    }, 200);
                                  }}
                                  color="error"
                                  aria-label="delete"
                                >
                                  <Delete />
                                </IconButton>
                              </Stack>
                            }
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  </TableBody>
                </Table>
              )}
            </div>
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
};

export default Brands;
