import React, { useEffect, useState } from "react";
import { message } from "antd";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import {
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
} from "@material-ui/core";
// @mui/material components
import {
  Button,
  Box,
  TextField,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Stack,
  IconButton,
  DialogContentText,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";

// core components
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";

// other components
import styles2 from "assets/jss/material-dashboard-react/components/tableStyle.js";
import RegularButton from "components/CustomButtons/Button";
import {
  getAllSchedulesAPI,
  addScheduleAPI,
  getScheduleByIdAPI,
  updateScheduleAPI,
  deleteScheduleAPI,
} from "services";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "bold",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);
const useStyles2 = makeStyles(styles2);

const Schedules = () => {
  const classes = useStyles();
  const classes2 = useStyles2();

  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);
  const [open3, setOpen3] = useState(false);
  const [updateM, setUpdateM] = useState(false);
  const [loading, setLoading] = useState(true);

  const [id, setId] = useState();
  const [dayName, setDayName] = useState("");
  const [startTime, setStartTime] = useState("");
  const [endTime, setEndTime] = useState("");
  const [logo, setLogo] = useState("");
  const [name, setName] = useState("");
  const [position, setPosition] = useState("");
  const [scheduleTitle, setScheduleTitle] = useState("");
  const [scheduleDescription, setScheduleDescription] = useState("");

  const [schedulesData, setSchedulesData] = useState([]);

  const [formErrors, setFormErrors] = useState([]);

  // ----------------------- Validation -------------------------
  const handleValidation = () => {
    let formErrors = {};
    let formIsValid = true;

    if (!dayName) {
      formIsValid = false;
      formErrors["dayName_error"] = "Day Name is required.";
    }
    if (!startTime) {
      formIsValid = false;
      formErrors["startTime_error"] = "Start Time is required.";
    }
    if (!endTime) {
      formIsValid = false;
      formErrors["endTime_error"] = "End Time is required.";
    }
    if (!logo) {
      formIsValid = false;
      formErrors["logo_error"] = "Logo is required.";
    }
    if (!name) {
      formIsValid = false;
      formErrors["name_error"] = "Name is required.";
    }
    if (!position) {
      formIsValid = false;
      formErrors["position_error"] = "Position is required.";
    }
    if (!scheduleTitle) {
      formIsValid = false;
      formErrors["scheduleTitle_error"] = "Schedule Title is required.";
    }
    if (!scheduleDescription) {
      formIsValid = false;
      formErrors["scheduleDescription_error"] =
        "Schedule Description is required.";
    }

    setFormErrors(formErrors);
    return formIsValid;
  };

  // ------- Reset Fields Function --------
  const resetFields = () => {
    setEndTime("");
    setDayName("");
    setScheduleDescription("");
    setStartTime("");
    setPosition("");
    setName("");
    setScheduleTitle("");
    setLogo("");
    setFormErrors([]);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    resetFields();
  };

  const handleOpen2 = () => {
    setOpen2(true);
  };

  const handleClose2 = () => {
    setOpen2(false);
    resetFields();
  };

  const handleClose3 = () => {
    setOpen3(false);
    resetFields();
  };

  const getAllSchedulesAPIF = async () => {
    getAllSchedulesAPI()
      .then((res) => {
        try {
          if (res.data.data) {
            setSchedulesData(res.data.data);
            setLoading(false);
          }
        } catch (error) {
          console.log("getAllSchedulesAPI catch error ", JSON.stringify(error));
        }
      })
      .catch((err) => {
        console.log("getAllSchedules err ", JSON.stringify(err));
      });
  };

  const getScheduleByIdAPIF = async (id) => {
    getScheduleByIdAPI(id)
      .then((res) => {
        try {
          if (res.data.data) {
            setId(res.data.data[0].id);
            setDayName(res.data.data[0].dayName);
            setEndTime(res.data.data[0].endTime);
            setScheduleDescription(res.data.data[0].scheduleDescription);
            setStartTime(res.data.data[0].startTime);
            setPosition(res.data.data[0].position);
            setName(res.data.data[0].name);
            setScheduleTitle(res.data.data[0].scheduleTitle);
            setLogo(res.data.data[0].logo);
          }
        } catch (error) {
          console.log("getScheduleByIdAPI catch error ", JSON.stringify(error));
        }
      })
      .catch((err) => {
        console.log("getScheduleByIdAPI err ", JSON.stringify(err));
      });
  };

  console.log(logo);

  const addScheduleAPIF = async () => {
    let data = new FormData();
    data.append("startTime", startTime);
    data.append("dayName", dayName);
    data.append("endTime", endTime);
    data.append("logo", logo);
    data.append("name", name);
    data.append("position", position);
    data.append("scheduleTitle", scheduleTitle);
    data.append("scheduleDescription", scheduleDescription);

    addScheduleAPI(data)
      .then((res) => {
        console.log("addSchedule res ", JSON.stringify(res));
        handleClose();
        message.success("Schedule added successfully");
        getAllSchedulesAPIF();
      })
      .catch((err) => {
        console.log("addSchedule err ", JSON.stringify(err));
      });
  };

  const updateScheduleAPIF = async () => {
    let data = new FormData();
    data.append("id", id);
    data.append("startTime", startTime);
    data.append("dayName", dayName);
    data.append("endTime", endTime);
    data.append("logo", logo);
    data.append("name", name);
    data.append("position", position);
    data.append("scheduleTitle", scheduleTitle);
    data.append("scheduleDescription", scheduleDescription);

    updateScheduleAPI(data)
      .then((res) => {
        console.log("updateSchedule res ", JSON.stringify(res));
        handleClose2();
        message.success("Schedule updated successfully");
        getAllSchedulesAPIF();
      })
      .catch((err) => {
        console.log("updateSchedule err ", JSON.stringify(err));
      });
  };

  const deleteScheduleAPIF = async () => {
    await deleteScheduleAPI(id)
      .then((res) => {
        console.log("deleteSchedule res ", JSON.stringify(res));
        handleClose3();
        message.success("Schedule deleted successfully");
        getAllSchedulesAPIF();
      })
      .catch((err) => {
        console.log("deleteSchedule err ", JSON.stringify(err));
      });
  };

  useEffect(() => {
    getAllSchedulesAPIF();
  }, []);

  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <RegularButton
          color="primary"
          size="sm"
          style={{ float: "right", marginBottom: 40 }}
          onClick={handleOpen}
        >
          Add Schedule
        </RegularButton>
        <Dialog fullWidth open={open} onClose={handleClose}>
          <DialogTitle marginBottom={2} textAlign="center">
            Add Schedule
          </DialogTitle>
          <DialogContent>
            <Stack spacing={1}>
              <Button fullWidth variant="contained" component="label">
                Upload Logo*
                <input
                  type="file"
                  accept="image/*"
                  hidden
                  onChange={(e) => {
                    setLogo(e.target.files[0]);
                  }}
                />
              </Button>
              {logo ? <p>Image Uploaded Successfully.</p> : null}
              {formErrors.logo_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.logo_error}
                  </p>
                </div>
              )}
              <FormControl variant="standard">
                <InputLabel id="demo-simple-select-standard-label">
                  Day Name*
                </InputLabel>
                <Select
                  labelId="demo-simple-select-standard-label"
                  id="demo-simple-select-standard"
                  value={dayName}
                  onChange={(e) => {
                    setDayName(e.target.value);
                  }}
                  label="Day Name"
                  fullWidth
                >
                  <MenuItem value="March 2, 2023">March 2, 2023</MenuItem>
                  <MenuItem value="March 3, 2023">March 3, 2023</MenuItem>
                </Select>
              </FormControl>
              {formErrors.dayName_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.dayName_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="name"
                label="Name*"
                type="text"
                fullWidth
                variant="standard"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              {formErrors.name_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.name_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="position"
                label="Position*"
                type="text"
                fullWidth
                variant="standard"
                value={position}
                onChange={(e) => {
                  setPosition(e.target.value);
                }}
              />
              {formErrors.position_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.position_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="startTime"
                label="Start Time*"
                type="time"
                step="3600"
                fullWidth
                variant="standard"
                value={startTime}
                onChange={(e) => {
                  setStartTime(e.target.value);
                }}
              />
              {formErrors.startTime_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.startTime_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="endTime"
                label="End Time*"
                type="time"
                fullWidth
                variant="standard"
                value={endTime}
                onChange={(e) => {
                  setEndTime(e.target.value);
                }}
              />
              {formErrors.endTime_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.endTime_error}
                  </p>
                </div>
              )}

              <TextField
                margin="dense"
                id="scheduleTitle"
                label="Schedule Title*"
                type="text"
                fullWidth
                variant="standard"
                value={scheduleTitle}
                onChange={(e) => {
                  setScheduleTitle(e.target.value);
                }}
              />
              {formErrors.scheduleTitle_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.scheduleTitle_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="scheduleDescription"
                label="Schedule Description*"
                type="text"
                fullWidth
                variant="standard"
                value={scheduleDescription}
                onChange={(e) => {
                  setScheduleDescription(e.target.value);
                }}
              />
              {formErrors.scheduleDescription_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.scheduleDescription_error}
                  </p>
                </div>
              )}
            </Stack>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleClose()}>Cancel</Button>
            <Button
              onClick={() => {
                if (handleValidation()) {
                  addScheduleAPIF();
                }
              }}
            >
              Proceed
            </Button>
          </DialogActions>
        </Dialog>
        <Dialog fullWidth open={open2} onClose={handleClose2}>
          <DialogTitle marginBottom={2} textAlign="center">
            Update Schedule
          </DialogTitle>
          <DialogContent>
            <Stack spacing={1}>
              <Button fullWidth variant="contained" component="label">
                Upload Logo*
                <input
                  type="file"
                  accept="image/*"
                  hidden
                  onChange={(e) => {
                    setLogo(e.target.files[0]);
                    setUpdateM(true);
                  }}
                />
              </Button>
              {updateM && logo ? (
                <p>Logo Uploaded Successfully.</p>
              ) : logo ? (
                <p>Logo Already Available.</p>
              ) : null}
              {formErrors.logo_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.logo_error}
                  </p>
                </div>
              )}
              <FormControl variant="standard">
                <InputLabel id="demo-simple-select-standard-label">
                  Day Name*
                </InputLabel>
                <Select
                  labelId="demo-simple-select-standard-label"
                  id="demo-simple-select-standard"
                  value={dayName}
                  onChange={(e) => {
                    setDayName(e.target.value);
                  }}
                  label="Day Name"
                  fullWidth
                >
                  <MenuItem value="March 2, 2023">March 2, 2023</MenuItem>
                  <MenuItem value="March 3, 2023">March 3, 2023</MenuItem>
                </Select>
              </FormControl>
              {formErrors.dayName_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.dayName_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="name"
                label="Name*"
                type="text"
                fullWidth
                variant="standard"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              {formErrors.name_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.name_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="position"
                label="Position*"
                type="text"
                fullWidth
                variant="standard"
                value={position}
                onChange={(e) => {
                  setPosition(e.target.value);
                }}
              />
              {formErrors.position_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.position_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="startTime"
                label="Start Time*"
                type="time"
                fullWidth
                variant="standard"
                value={startTime}
                onChange={(e) => {
                  setStartTime(e.target.value);
                }}
              />
              {formErrors.startTime_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.startTime_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="endTime"
                label="End Time*"
                type="time"
                fullWidth
                variant="standard"
                value={endTime}
                onChange={(e) => {
                  setEndTime(e.target.value);
                }}
              />
              {formErrors.endTime_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.endTime_error}
                  </p>
                </div>
              )}

              <TextField
                margin="dense"
                id="scheduleTitle"
                label="Schedule Title*"
                type="text"
                fullWidth
                variant="standard"
                value={scheduleTitle}
                onChange={(e) => {
                  setScheduleTitle(e.target.value);
                }}
              />
              {formErrors.scheduleTitle_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.scheduleTitle_error}
                  </p>
                </div>
              )}
              <TextField
                margin="dense"
                id="scheduleDescription"
                label="Schedule Description*"
                type="text"
                fullWidth
                variant="standard"
                value={scheduleDescription}
                onChange={(e) => {
                  setScheduleDescription(e.target.value);
                }}
              />
              {formErrors.scheduleDescription_error && (
                <div className="offset-0">
                  <p
                    style={{
                      color: "red",
                      fontSize: ".8rem",
                      marginTop: "0px",
                    }}
                  >
                    {formErrors.scheduleDescription_error}
                  </p>
                </div>
              )}
            </Stack>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleClose2()}>Cancel</Button>
            <Button
              onClick={() => {
                if (handleValidation()) {
                  updateScheduleAPIF();
                }
              }}
            >
              Proceed
            </Button>
          </DialogActions>
        </Dialog>
        <Dialog
          open={open3}
          onClose={handleClose3}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">Delete Schedule</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              Are You Sure, You want to delete this Schedule?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose3}>No</Button>
            <Button onClick={() => deleteScheduleAPIF()}>Yes</Button>
          </DialogActions>
        </Dialog>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>Schedules</h4>
          </CardHeader>
          <CardBody>
            <div className={classes2.tableResponsive}>
              {loading ? (
                <Box sx={{ display: "flex" }}>
                  <CircularProgress color="secondary" />
                </Box>
              ) : (
                <Table className={classes2.table}>
                  <TableHead className={classes2["primary" + "TableHeader"]}>
                    <TableRow className={classes2.tableHeadRow}>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        S.No.
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Logo
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Day Name
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Speaker Name
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Position
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Start Time
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        End Time
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Title
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Description
                      </TableCell>
                      <TableCell
                        className={
                          classes2.tableCell + " " + classes2.tableHeadCell
                        }
                      >
                        Actions
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {schedulesData?.map((data, index) => {
                      let a = data.logo;
                      let b = "/";
                      let position = 6;
                      let images = [
                        a?.slice(0, position),
                        b,
                        a?.slice(position),
                      ]?.join("");

                      return (
                        <TableRow
                          key={data.id}
                          className={classes2.tableBodyRow}
                        >
                          <TableCell className={classes2.tableCell}>
                            {index + 1}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            <img
                              // src={`http://localhost:8000/${images}`}
                              src={`/${images}`}
                              style={{ height: 40, width: 40 }}
                            ></img>
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.dayName}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.name}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.position}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.startTime}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.endTime}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.scheduleTitle}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {data.scheduleDescription}
                          </TableCell>
                          <TableCell className={classes2.tableCell}>
                            {
                              <Stack direction="row">
                                <IconButton
                                  onClick={() => {
                                    getScheduleByIdAPIF(data.id);
                                    setTimeout(() => {
                                      handleOpen2();
                                    }, 500);
                                  }}
                                  color="primary"
                                  aria-label="edit"
                                >
                                  <Edit />
                                </IconButton>
                                <IconButton
                                  onClick={() => {
                                    getScheduleByIdAPIF(data.id);
                                    setTimeout(() => {
                                      setOpen3(true);
                                    }, 200);
                                  }}
                                  color="error"
                                  aria-label="delete"
                                >
                                  <Delete />
                                </IconButton>
                              </Stack>
                            }
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  </TableBody>
                </Table>
              )}
            </div>
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
};

export default Schedules;
